<template>
  <div>
    <header id='header-nav'>
      <div class="header-back" v-if="back" @click="$goToRoute(back)"></div>
      <div class="header-title">
        <span class="header-title__text">{{title}}</span>
      </div>
    </header>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {}
    },
    props:['back','title'],
    methods:{
      goBack:function(){
        $route.push(back)
      }
    }
  }

</script>

<style lang="scss" rel="stylesheet/scss" >
  @import "src/style/base.scss";
  #header-nav{
    background-color: $XaColorAirBlue;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width:100%;
    height:$XaHeaderHeight
  }
  .header-back{
    color:#fff;
    width: 1.5rem;
    height: 100%;
    line-height: $XaHeaderHeight;
    text-align:center;
    &:after{
      content: " ";
      @include setArrow(0.4rem, #fff, 2px,left);
    }
  }
  .header-title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    color: #fff;
    text-align: center
  }
  .header-title__text{
    font-size:0.8rem;
    font-color:#fff;
    color:#fff;
    text-align: center;
    font-weight: bold;
  }
</style>



