<template>
  <div class="xa-page">
    <head-top>8401</head-top>
    <div>
      <div class="flight-header">
        <div class="flight-header__nav xa-nav" style="display:flex;">
          <a :class="{active: filter=='inn'}" @click="filter='inn'">进港</a>
          <a :class="{active: filter=='out'}" @click="filter='out'">出港</a>
          <a :class="{active: filter=='care'}" @click="filter='care'">关注</a>
          <a :class="{active: filter=='vip'}" @click="filter='vip'">要客</a>
        </div>
        <table class="flight-header__table">
          <colgroup>
            <col width="18%"/>
            <col width="20%"/>
            <col width="17%"/>
            <col width="15%"/>
            <col width="15%"/>
            <col width="15%"/>
          </colgroup>
          <tr>
            <th>航班</th>
            <th>航线</th>
            <th>机位</th>
            <th>起飞</th>
            <th>到达</th>
            <th>状态</th>
          </tr>
        </table>
      </div>
      <scroller class="flight-content">
        <table class="flight-content__table">
          <colgroup>
            <col width="18%"/>
            <col width="20%"/>
            <col width="17%"/>
            <col width="15%"/>
            <col width="15%"/>
            <col width="15%"/>
          </colgroup>
          <tr v-for="f in flightsOnShow" class="needsclick" @click="viewFlightDetail(f)">
            <td class="flight-no">{{f.FlightNo}}<br>{{f.AcReg}}</td>
            <td>{{f.DepaCity}}<br>{{f.ArriCity}}</td>
            <td>7<br>17</td>
            <td><span class="st">13:00</span><br><span class="et">13:20</span></td>
            <td><span class="st">15:30</span><br><span class="et">15:50</span></td>
            <td>起飞</td>
          </tr>
        </table>
      </scroller>
    </div>
    <footer-guide></footer-guide>
  </div>
</template>


<script>
  import footerGuide from '@/components/Footer'
  import headTop from '@/components/Header'
  export default{
    data(){
      return {
        flightsOnShow: [],
        flights: [],
        filter: 'inn'
      }
    },
    methods: {
      log: function (e) {
        console.log(e)
      },
      getFlights: function () {
        var array = []
        for (var i = 0; i < 100; i++) {
          array.push({
            FlightNo: "MF8101",
            AcReg: "B-2761",
            DepaCity: "厦门" + (i == 0 ? 'x' : ''),
            ArriCity: "北京",
            Std: "13:00",
            Etd: "13:20",
            Sta: "15:30",
            Eta: "15:50",
            Status: "起飞",
            Vip: i % 20 == 0,
            Care: i % 15 == 0
          });
          array.push({
            FlightNo: "MF8101",
            AcReg: "B-2762",
            DepaCity: "北京",
            ArriCity: "厦门",
            Std: "16:00",
            Etd: "16:20",
            Sta: "19:30",
            Eta: "19:50",
            Status: "起飞"
          })
        }
        this.flightsOnShow = this.flights = array

      },
      viewFlightDetail: function (f) {
        this.$router.push("/flight/xxx/support")
      }
    },
    watch: {
      filter: function (val, oldVal) {
        if (val == 'inn') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.DepaCity == "厦门"
          })
        }
        if (val == 'out') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.ArriCity == "厦门"
          })
        }
        if (val == 'care') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Care
          })
        }
        if (val == 'vip') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Vip
          })
        }
      }
    },
    components: {
      footerGuide,
      headTop
    },
    created: function () {
      this.getFlights()
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";
  table {
    color: #333333;
    table-layout: fixed;
    border-collapse: collapse;
    font-family: 黑体;
    width: 100%;
    text-align: center;
  }

  tr {
    border-bottom: 1px solid #ECECEC;
  }

  .flight-header__table > tr {
    border-top: 1px solid #ECECEC;
  }

  th, td {
    padding: 0.2rem 0.2rem;
  }

  .flight-header {
    background-color: #fff;
    margin-top:$XaHeaderHeight;
    width: 100%;
    z-index: 100;
    position: fixed;
    left: 0;
    font-size: 0.6rem;
    &__table {
      width: 100%;
      height: 1.2rem;
    }
  }

  .flight-content {
    font-size: 0.6rem;
    margin-top: 4.4rem;
    margin-bottom: 2rem;
    height:calc(100% - 6.4rem);
    width:100%;
    &__table{
      background-color:#fff;
    }
  }

  .st {
    /*color:#ea4335;*/
  }

  .et {
    /*color:#BB9C05;*/
  }

</style>



